.buttonwrapper button {
  margin: 0px 8px 0px 8px;
  width: 128px;
  height: 40px;
  color: #fff;
  cursor: pointer;
}

.buttonwrapper button:nth-of-type(1){
  background-color: red;
}
.buttonwrapper button:nth-of-type(2){
  background-color: green;
}

.boxwrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.boxwrapper div{
  margin:  1px;
  width: 20px;
  height: 20px;
  color: #fff;
}
.boxwrapper div:nth-child(odd) {
    background: #000000;
}

.boxwrapper div:nth-child(even) {
    background: #0000ff;
}

.boxwrapper div:nth-child(5n) {
     border-top: 4px solid #FFA500
}



